import { Grid, Typography } from '@mui/material'
import { Box } from '@mui/system'

const HeroeImage = ({ urlImg, title, subTitle }) => {
  return (
    <Grid container sx={{ mt: '60px' }}>
      <Grid item xs={12} sx={{ position: 'relative', height: '500px' }}>
        <Box
          width="100%"
          height="100%"
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          textAlign="center"
          gap={3}
          p={13}
          sx={{
            backgroundImage: `url(${urlImg})`,
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
            backgroundSize: 'cover',
          }}
        >
          <Typography
            variant="h3"
            component="h1"
            color="white"
            sx={{ fontWeight: 'bold', letterSpacing: 3 }}
          >
            {title}
          </Typography>
          <Typography
            variant="body1"
            component="p"
            lineHeight={2}
            color="white"
          >
            {subTitle}
          </Typography>
        </Box>
      </Grid>
    </Grid>
  )
}

export default HeroeImage
