import { Button, Typography } from '@mui/material'
import { Box } from '@mui/system'
import MenuIcon from '@mui/icons-material/Menu'
import DarkModeIcon from '@mui/icons-material/DarkMode'
import { useContext, useState } from 'react'
import GlobalStylesAppContext from '../contexts/GlobalStylesApp.context'
import { Link } from 'react-router-dom'
import { useTheme } from '@mui/material/styles'
import useMediaQuery from '@mui/material/useMediaQuery'

const stylesMenuMdUp = {
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  gap: 5,
}

const stylesMenuMdDown = {
  display: 'flex',
  flexDirection: 'column',
  position: 'absolute',
  top: 60,
  right: '-150%',
  justifyContent: 'center',
  alignItems: 'center',
  padding: '2rem',
  backgroundColor: 'background.main',
  width: '100%',
  transition: '0.5s all ease',
}

const urlNav = [
  {
    id: 1,
    url: '/',
    title: 'Inicio',
  },
  {
    id: 2,
    url: '/contacto',
    title: 'Contacto',
  },
  {
    id: 3,
    url: '/portafolio',
    title: 'Portafolio',
  },
  {
    id: 4,
    url: '/login',
    title: 'Login',
  },
]

const Nav = () => {
  const { changeDarkMode } = useContext(GlobalStylesAppContext)
  const theme = useTheme()
  const matchesMdUp = useMediaQuery(theme.breakpoints.up('md'))
  const [openMenu, setOpenMenu] = useState(false)

  const changeOpenMenu = () => {
    !openMenu
      ? (stylesMenuMdDown.right = '0')
      : (stylesMenuMdDown.right = '-150%')
    setOpenMenu(!openMenu)
  }

  return (
    <header>
      <nav>
        <Box
          zIndex={100}
          position="fixed"
          top={0}
          left={0}
          width="100%"
          height="60px"
          display="flex"
          alignItems="center"
          justifyContent="space-between"
          pl={5}
          pr={5}
          bgcolor="backgroundTransparent.main"
          sx={{ backdropFilter: 'blur(5px)' }}
        >
          <Typography variant="h5" component="h2" sx={{ fontWeight: 'bold' }}>
            Tienda
          </Typography>
          <Box display="flex" gap={2}>
            <Box sx={matchesMdUp ? stylesMenuMdUp : stylesMenuMdDown}>
              {urlNav.map((item) => (
                <Button component={Link} to={item.url} key={item.id}>
                  {item.title}
                </Button>
              ))}
            </Box>
            <Button
              variant="outlined"
              onClick={changeDarkMode}
              sx={{ minWidth: 'min-content' }}
            >
              <DarkModeIcon fontSize="medium" />
            </Button>

            <Button
              variant="outlined"
              onClick={changeOpenMenu}
              sx={{
                minWidth: 'min-content',
                display: matchesMdUp ? 'none' : 'flex',
              }}
            >
              <MenuIcon fontSize="medium" />
            </Button>
          </Box>
        </Box>
      </nav>
    </header>
  )
}

export default Nav
