import { createTheme, useMediaQuery } from '@mui/material'
import { ThemeProvider } from '@mui/system'
import { createContext, useEffect, useState } from 'react'

const GlobalStylesAppContext = createContext()

const GlobalStylesAppProvider = ({ children }) => {
  const darkModePrefers = useMediaQuery('(prefers-color-scheme: dark)')
  const [darkMode, setDarkMode] = useState(darkModePrefers)

  let theme = createTheme({
    palette: {
      mode: darkMode ? 'dark' : 'light',
      primary: {
        main: '#00dda5',
      },
      secondary: {
        main: '#ffbe33',
      },
      background: {
        main: darkMode ? '#121212' : '#fff',
        dark: '#121212',
        light: '#fff',
      },
      iconBlack: {
        main: 'rgba(0, 0, 0, 0.54)',
      },
      backgroundTransparent: {
        main: darkMode ? 'rgba(18,18,18,0.7)' : 'rgba(255,255,255,0.7)',
        dark: 'rgba(18,18,18,0.7)',
        light: 'rgba(255,255,255,0.7)',
      },
      border: {
        main: '#f7f7f7',
      },
    },
    components: {
      MuiCssBaseline: {
        styleOverrides: {
          body: {
            backgroundColor: darkMode ? '#121212' : '#F1F1F1',
          },
        },
      },
    },
  })

  useEffect(() => {
    setDarkMode(darkModePrefers)
  }, [darkModePrefers])

  const changeDarkMode = () => {
    setDarkMode(!darkMode)
  }

  const data = { changeDarkMode }

  return (
    <ThemeProvider theme={theme}>
      <GlobalStylesAppContext.Provider value={data}>
        {children}
      </GlobalStylesAppContext.Provider>
    </ThemeProvider>
  )
}

export default GlobalStylesAppContext
export { GlobalStylesAppProvider }
