const globalStyles = {
  html: {
    boxSizing: 'border-box',
  },
  '*,*::before,*::after': {
    boxSizing: 'inherit',
  },
  body: {
    padding: '0',
    margin: '0',
    width: '100%',
    height: '100%',
  },
  img: {
    width: '100%',
    height: '100%',
    display: 'block',
  },
}

export default globalStyles
