import { Container, Grid, Paper, TextField, Typography } from '@mui/material'
import { Box } from '@mui/system'
import CallIcon from '@mui/icons-material/Call'
import MapIcon from '@mui/icons-material/Map'
import FacebookIcon from '@mui/icons-material/Facebook'
import EmailIcon from '@mui/icons-material/Email'
import InstagramIcon from '@mui/icons-material/Instagram'
import GeneralButton from '../components/GeneralButton'
import TitleCards from '../components/TitleCards'

const ContactCard = () => {
  return (
    <Container maxWidth="lg" sx={{ pt: 5, pb: 5 }}>
      <Paper sx={{ overflow: 'hidden' }}>
        <Grid container>
          <Grid item xs={12} md={5}>
            <Box
              p={4}
              display="flex"
              flexDirection="column"
              justifyContent="center"
              gap={2}
              height="100%"
              width="100%"
              bgcolor="primary.main"
            >
              <Box display="flex" gap={2}>
                <CallIcon color="iconBlack" />
                <Typography variant="body1" component="p">
                  +58485985965
                </Typography>
              </Box>
              <Box display="flex" gap={2}>
                <MapIcon color="iconBlack" />
                <Typography variant="body1" component="p">
                  393 AVENUE
                  <br /> C BAYONNE NJ <br /> 07002-5145 <br /> USA
                </Typography>
              </Box>
              <Box display="flex" gap={2}>
                <FacebookIcon color="iconBlack" />
                <Typography variant="body1" component="p">
                  /Dise単oGrafico
                </Typography>
              </Box>
              <Box display="flex" gap={2}>
                <EmailIcon color="iconBlack" />
                <Typography variant="body1" component="p">
                  Dise単oGrafico@gmail.com
                </Typography>
              </Box>
              <Box display="flex" gap={2}>
                <InstagramIcon color="iconBlack" />
                <Typography variant="body1" component="p">
                  /Dise単oGrafico
                </Typography>
              </Box>
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d15813.112330808895!2d-72.22379388914794!3d7.7603045325947155!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2sve!4v1639583044111!5m2!1sen!2sve"
                style={{ border: 0, width: '100%', minHeight: '250px' }}
                allowFullScreen=""
                loading="lazy"
                title="Mapa"
              ></iframe>
            </Box>
          </Grid>
          <Grid item xs={12} md={7}>
            <Box
              p={4}
              component="form"
              display="flex"
              flexDirection="column"
              gap={2}
              justifyContent="center"
              height="100%"
            >
              <TitleCards msg="Escribenos" />
              <TextField id="Nombre" label="Nombre" variant="outlined" />
              <TextField id="Email" label="Email" variant="outlined" />
              <TextField id="Mensaje" label="Mensaje" multiline rows={4} />
              <GeneralButton msg="Enviar" />
            </Box>
          </Grid>
        </Grid>
      </Paper>
    </Container>
  )
}

export default ContactCard
