import { Container, Grid, Paper, Typography } from '@mui/material'
import AccessTimeOutlinedIcon from '@mui/icons-material/AccessTimeOutlined'
import MonetizationOnOutlinedIcon from '@mui/icons-material/MonetizationOnOutlined'
import WorkOutlineIcon from '@mui/icons-material/WorkOutline'
import { Box } from '@mui/system'
import TitleCards from './TitleCards'

const servicesData = [
  {
    id: 1,
    icon: <AccessTimeOutlinedIcon color="primary" sx={{ fontSize: 75 }} />,
    title: 'Creaciones Rapidas',
    body:
      'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Magni non veniam earum pariatur sequi natus temporibus, quos animi aut at, eum repellendus. Dolorem nemo odio ducimus maiores expedita,quo cum?',
  },
  {
    id: 2,
    icon: <MonetizationOnOutlinedIcon color="primary" sx={{ fontSize: 75 }} />,
    title: 'Precios Bajos',
    body:
      'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Magni non veniam earum pariatur sequi natus temporibus, quos animi aut at, eum repellendus. Dolorem nemo odio ducimus maiores expedita,quo cum?',
  },
  {
    id: 3,
    icon: <WorkOutlineIcon color="primary" sx={{ fontSize: 75 }} />,
    title: 'Equipo Profesional',
    body:
      'Lorem ipsum dolor, sit amet consectetur adipisicing elit. Magni non veniam earum pariatur sequi natus temporibus, quos animi aut at, eum repellendus. Dolorem nemo odio ducimus maiores expedita,quo cum?',
  },
]

const Services = () => {
  return (
    <Container sx={{ pt: 5, pb: 5 }}>
      <Grid container spacing={2}>
        {servicesData.map((item) => (
          <Grid item xs={12} md={4} sx={{ minHeight: '200px' }} key={item.id}>
            <Paper>
              <Box
                p={5}
                display="flex"
                flexDirection="column"
                justifyContent="center"
                alignItems="center"
                gap={5}
              >
                {item.icon}
                <TitleCards msg={item.title} />
                <Typography
                  variant="body1"
                  component="p"
                  align="center"
                  sx={{ fontWeight: 'lighter' }}
                >
                  {item.body}
                </Typography>
              </Box>
            </Paper>
          </Grid>
        ))}
      </Grid>
    </Container>
  )
}

export default Services
