import { Button, Paper, TextField } from '@mui/material'
import { Box } from '@mui/system'
import { useState } from 'react'
import GeneralButton from '../components/GeneralButton'
import TitleCards from '../components/TitleCards'

const initialStateLogin = false

const LoginForm = () => {
  const [register, setRegister] = useState(initialStateLogin)

  const changeLoginView = () => {
    setRegister(!register)
    console.log(register)
  }

  return (
    <Paper>
      <Box component="form" display="flex" flexDirection="column" p={4} gap={4}>
        <TitleCards msg={register ? 'Registrarme' : 'Ingresar'} />
        <TextField id="user" label="Usuario" type="text" />
        <TextField id="pass" label="Contrase単a" type="password" />
        <Button
          variant="text"
          sx={{ width: 'max-content', alignSelf: 'center' }}
          onClick={changeLoginView}
        >
          {register ? 'Ingresar' : 'Registrarme'}
        </Button>
        <GeneralButton msg={register ? 'Registrarme' : 'Ingresar'} />
      </Box>
    </Paper>
  )
}

export default LoginForm
