import {
  Drawer,
  List,
  ListItemText,
  Divider,
  ListItemIcon,
  Typography,
  IconButton,
  Button,
  ListItemButton,
  Collapse,
} from '@mui/material'
import { Box } from '@mui/system'
import { styled } from '@mui/material/styles'
import MenuIcon from '@mui/icons-material/Menu'
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft'
import { useContext, useState } from 'react'
import DarkModeIcon from '@mui/icons-material/DarkMode'
import GlobalStylesAppContext from '../contexts/GlobalStylesApp.context'
import StyleIcon from '@mui/icons-material/Style'
import WorkIcon from '@mui/icons-material/Work'
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown'
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp'
import TitleCards from '../components/TitleCards'
import CircleIcon from '@mui/icons-material/Circle'

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(1, 1),
  justifyContent: 'space-between',
}))

const DrawerBottom = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  padding: theme.spacing(1, 1),
  justifyContent: 'center',
}))

const initialOpen = true
const initialOpenAppearance = false

const Dashboard = () => {
  const [open, setOpen] = useState(initialOpen)
  const [openAppearance, setOpenAppearance] = useState(initialOpenAppearance)
  const { changeDarkMode } = useContext(GlobalStylesAppContext)

  const handleOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleChangeOpenAppearance = () => {
    setOpenAppearance(!openAppearance)
  }

  return (
    <>
      <Box sx={{ display: 'flex' }}>
        <Box
          position="fixed"
          display="flex"
          top={0}
          left={0}
          justifyContent="space-between"
          alignItems="center"
          height="57px"
          width={open ? 'calc(100% - 240px)' : '100%'}
          pl={2}
          pr={2}
          ml={open ? '240px' : 0}
          bgcolor="background.main"
          sx={{ transition: '0.2s margin ease' }}
        >
          <Box display="flex" alignItems="center" gap={1}>
            <IconButton
              aria-label="open drawer"
              edge="start"
              sx={{ mr: 2, ...(open && { display: 'none' }) }}
              onClick={handleOpen}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap component="h1">
              Dashboard
            </Typography>
          </Box>
          <Button
            variant="outlined"
            onClick={changeDarkMode}
            sx={{ minWidth: 'min-content' }}
          >
            <DarkModeIcon fontSize="medium" />
          </Button>
        </Box>
        <Drawer
          width="240px"
          variant="persistent"
          anchor="left"
          open={open}
          sx={{
            '& .MuiDrawer-paper': {
              width: '240px',
            },
            flexShrink: 0,
          }}
        >
          <DrawerHeader>
            <TitleCards msg="Tienda" />
            <IconButton
              sx={{ ...(!open && { display: 'none' }) }}
              onClick={handleClose}
            >
              <ChevronLeftIcon />
            </IconButton>
          </DrawerHeader>
          <Divider />
          <List
            sx={{
              flexGrow: 1,
              ml: 1,
              mr: 1,
              color: 'text.secondary',
              '&& .Mui-selected, && .Mui-selected:hover': {
                '&, & .MuiListItemIcon-root': {
                  color: 'primary.main',
                },
              },
            }}
          >
            <ListItemButton
              onClick={handleChangeOpenAppearance}
              selected={openAppearance}
              sx={{ borderRadius: '1rem' }}
            >
              <ListItemIcon>
                <StyleIcon fontSize="small" />
              </ListItemIcon>
              <ListItemText primary="Apariencia" />
              {openAppearance ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />}
            </ListItemButton>
            <Collapse in={openAppearance} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItemButton sx={{ pl: 4, borderRadius: '1rem' }}>
                  <ListItemIcon>
                    <CircleIcon sx={{ fontSize: 10 }} />
                  </ListItemIcon>
                  <ListItemText primary="Trabajos" />
                </ListItemButton>
              </List>
            </Collapse>
          </List>
          <Divider />
          <DrawerBottom>
            <Typography
              variant="body3"
              component="small"
              color="text.secondary"
            >
              App v0.0.1
            </Typography>
          </DrawerBottom>
        </Drawer>
        <Box
          component="main"
          sx={{
            flexGrow: 1,
            padding: 2,
            mt: '65px',
            ml: '240px',
            transition: '0.2s all ease',
            ...(!open && { ml: 0 }),
          }}
        >
          Lorem ipsum, dolor sit amet consectetur adipisicing elit. Labore ipsum
          magnam quasi, est atque architecto expedita dolorum dolorem
          asperiores. Voluptatem esse atque rerum nostrum delectus dolores
          architecto earum, aspernatur ad!Lorem ipsum, dolor sit amet
          consectetur adipisicing elit. Labore ipsum magnam quasi, est atque
          architecto expedita dolorum dolorem asperiores. Voluptatem esse atque
          rerum nostrum delectus dolores architecto earum, aspernatur ad!Lorem
          ipsum, dolor sit amet consectetur adipisicing elit. Labore ipsum
          magnam quasi, est atque architecto expedita dolorum dolorem
          asperiores. Voluptatem esse atque rerum nostrum delectus dolores
          architecto earum, aspernatur ad!Lorem ipsum, dolor sit amet
          consectetur adipisicing elit. Labore ipsum magnam quasi, est atque
          architecto expedita dolorum dolorem asperiores. Voluptatem esse atque
          rerum nostrum delectus dolores architecto earum, aspernatur ad!
        </Box>
      </Box>
    </>
  )
}

export default Dashboard
