import { Button } from '@mui/material'
import { PropTypes } from 'prop-types'

const GeneralButton = ({ color, msg }) => {
  return (
    <Button
      variant="contained"
      color={color}
      sx={{
        borderRadius: '2rem',
        boxShadow: 'none',
        pl: 3,
        pr: 3,
        pt: 1.2,
        pb: 1.2,
      }}
    >
      {msg}
    </Button>
  )
}

GeneralButton.defaultProp = {
  color: 'primary',
  msg: 'button',
}

GeneralButton.propTypes = {
  color: PropTypes.string,
  msg: PropTypes.string,
}

export default GeneralButton
