import {
  ImageList,
  ImageListItem,
  ImageListItemBar,
  useMediaQuery,
  useTheme,
} from '@mui/material'
import Proptypes from 'prop-types'

const ImageListGenerator = ({ imagesData }) => {
  const theme = useTheme()
  const match = useMediaQuery(theme.breakpoints.up('md'))

  return (
    <ImageList
      variant="masonry"
      cols={match ? 3 : 2}
      gap={8}
      sx={{ mt: 1, mb: 1 }}
    >
      {imagesData.map((item) => (
        <ImageListItem key={item.id}>
          <img src={item.img} alt={item.title} />
          <ImageListItemBar title={item.title} subtitle={item.subtitle} />
        </ImageListItem>
      ))}
    </ImageList>
  )
}

ImageListGenerator.defaultProps = {
  imagesData: [],
}

ImageListGenerator.propTypes = {
  imagesData: Proptypes.array.isRequired,
}

export default ImageListGenerator
