import { Grid, Typography } from '@mui/material'

import { Box } from '@mui/system'
import PersonIcon from '@mui/icons-material/Person'
import EditIcon from '@mui/icons-material/Edit'
import InsertEmoticonIcon from '@mui/icons-material/InsertEmoticon'

const someNumbersData = [
  {
    id: 1,
    icon: <PersonIcon color="iconBlack" sx={{ fontSize: '4rem' }} />,
    number: 25,
    title: 'CLIENTES',
  },
  {
    id: 2,
    icon: <EditIcon color="iconBlack" sx={{ fontSize: '4rem' }} />,
    number: 50,
    title: 'TRABAJOS',
  },
  {
    id: 3,
    icon: <InsertEmoticonIcon color="iconBlack" sx={{ fontSize: '4rem' }} />,
    number: 100,
    title: '% SATISFACCIN',
  },
]

const SomeNumbers = () => {
  return (
    <Grid container bgcolor="primary.main">
      {someNumbersData.map((item) => (
        <Grid item xs={12} md={4} sx={{ p: 5 }} key={item.id}>
          <Box
            height="max-content"
            key={item.id}
            display="flex"
            flexDirection="column"
            alignItems="center"
          >
            {item.icon}
            <Typography
              variant="h2"
              component="h3"
              align="center"
              sx={{ fontWeight: 'bolder' }}
            >
              {item.number}
            </Typography>
            <Typography
              variant="h4"
              component="h4"
              align="center"
              color="iconBlack.main"
              sx={{ fontWeight: 'lighter' }}
            >
              {item.title}
            </Typography>
          </Box>
        </Grid>
      ))}
    </Grid>
  )
}

export default SomeNumbers
