import { Grid, Typography } from '@mui/material'
import { Box } from '@mui/system'
import VideoHeroe from '../assets/videos/heroe.mp4'
import FacebookIcon from '@mui/icons-material/Facebook'
import TwitterIcon from '@mui/icons-material/Twitter'
import InstagramIcon from '@mui/icons-material/Instagram'
import GeneralButton from './GeneralButton'

const videoStyles = {
  width: '100%',
  height: '100%',
  position: 'absolute',
  top: 0,
  left: 0,
  objectFit: 'cover',
  zIndex: -1,
}

const Heroe = () => {
  return (
    <Grid container sx={{ mt: '60px', pb: 5 }}>
      <Grid item xs={12} sx={{ position: 'relative', height: '500px' }}>
        <Box
          width="100%"
          height="100%"
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          textAlign="center"
          gap={3}
          p={13}
        >
          <Typography
            variant="h3"
            component="h1"
            color="white"
            sx={{ fontWeight: 'bold', letterSpacing: 3 }}
          >
            Tienda Dise単o Grafico
          </Typography>
          <Typography
            variant="body1"
            component="p"
            lineHeight={2}
            color="white"
          >
            Lorem ipsum dolor sit amet consectetur, adipisicing elit.
          </Typography>
          <GeneralButton color="primary" msg="Ver Trabajos" />
          <Box
            position="absolute"
            bottom={0}
            margin="0 auto"
            display="flex"
            p={2}
            gap={2}
          >
            <FacebookIcon fontSize="small" sx={{ color: 'white' }} />
            <TwitterIcon fontSize="small" sx={{ color: 'white' }} />
            <InstagramIcon fontSize="small" sx={{ color: 'white' }} />
          </Box>
        </Box>
        <video muted autoPlay loop style={videoStyles}>
          <source src={VideoHeroe} type="video/mp4" />
          Tu Navegador no es compatible con el tag de video de HTML
        </video>
      </Grid>
    </Grid>
  )
}

export default Heroe
