import { Container, Grid, Typography } from '@mui/material'
import { Box } from '@mui/system'
import AboutUsImage from '../assets/images/aboutus.jpg'
import TitleCards from './TitleCards'

const AboutUs = () => {
  return (
    <Container sx={{ pt: 5, pb: 5 }}>
      <Grid container>
        <Grid item xs={12} md={6} sx={{ height: '350px' }}>
          <Box
            p={4}
            display="flex"
            justifyContent="center"
            alignItems="flex-start"
            flexDirection="column"
            height="100%"
            width="100%"
          >
            <TitleCards msg="Sobre Nosotros" />
            <Typography
              variant="body1"
              component="p"
              mt={3}
              sx={{ fontWeight: 'lighter' }}
            >
              Lorem ipsum, dolor sit amet consectetur adipisicing elit.
              Voluptate, omnis numquam alias id obcaecati quaerat ducimus?
              Ducimus fugiat totam distinctio sunt. Eos ipsam aliquid ducimus
              labore eum? Aut, dolores repudiandae.
            </Typography>
          </Box>
        </Grid>
        <Grid item xs={12} md={6} sx={{ height: '350px' }}>
          <img
            src={AboutUsImage}
            alt="aboutus"
            title="about us"
            style={{ objectFit: 'cover' }}
          />
        </Grid>
      </Grid>
    </Container>
  )
}

export default AboutUs
